import React, { PropTypes } from 'react';
import { Icon, Bubble, ClothText, Cloth, Delete } from './styledComponents';
import pricing from '../pricing';
import assets from '../assets/assets';

const ClothAlterations = ({ orderItem, itemNumber, toggleChoices, deleteCloth }) => {
  const icon = assets[orderItem.slug];
  const slug = orderItem.slug;
  const cloth = pricing.find(clothIt => clothIt.slug === slug);
  const alterationsNumber = (Object.keys(orderItem.alterations || {})).length;
  return (
    <div style={{ position: 'relative' }}>
      <Cloth onClick={() => toggleChoices(cloth)}>
        <Icon className={`flaticon-${icon}`} />
        <ClothText>
          {cloth.cloth} n°{itemNumber}
        </ClothText>
        <Bubble>{alterationsNumber}</Bubble>
      </Cloth>
      {typeof deleteCloth === 'function' && <Delete onClick={deleteCloth}>-</Delete>}
    </div>
  );
};

ClothAlterations.propTypes = {
  itemNumber: PropTypes.number.isRequired,
  orderItem: PropTypes.shape({}).isRequired,
  toggleChoices: PropTypes.func.isRequired,
  deleteCloth: PropTypes.func,
};

ClothAlterations.defaultProps = {
  deleteCloth() {},
};

export default ClothAlterations;



